.farm-container {
	flex-direction: row;
}

@media screen and (max-width: 1050px) {
	.farm-container {
		flex-direction: column;
	}
}

.farm-container-sm {
	flex-direction: row;
}

@media screen and (max-width: 900px) {
	.farm-container-sm {
		flex-direction: column;
		column-gap: 0.5rem;
	}
}
.flowchart-container {
	width: 100%;
	height: 100%;
}
@media screen and (max-width: 900px) {
	.flowchart-container {
		width: 80%;
	}
}

.border-container {
	flex-direction: row;
	min-height: 300px;
	margin-top: 25px;
}

.border-container2 {
	flex-direction: row;
	min-height: 300px;
	margin-top: 10px;
	top: 10px;
	position: relative;
}

@media screen and (max-width: 900px) {
	.border-container {
		flex-direction: column;
		height: auto;
	}
}

.launchpad-container {
	flex-direction: row;
}

@media screen and (max-width: 680px) {
	.launchpad-container {
		flex-direction: column;
	}
}
.launchpad-container-sm {
	flex-direction: row;
}

@media screen and (max-width: 1430px) {
	.launchpad-container-sm {
		flex-direction: column;
	}
}

.text-tab {
	width: 50%;
}

@media screen and (max-width: 900px) {
	.text-tab {
		width: 100%;
	}
}

.countDown-box {
	justify-content: center;
	display: flex;
	padding: 0 1.3vh 1.5vh;
	align-items: center;
}

.countDown-number {
	flex-basis: 70px;
	justify-content: center;
}

.countDown-number-1 {
	flex-direction: row;
	color: #b5b5b6;
	color: white;
	color: white;
	color: var(--accent-color, white);
	width: 75px;
	height: 75px;
	border-radius: 5px;
	background-color: hsla(0, 0%, 82.7%, 0.3);
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 30px;
	margin: 20px 10px 0px 10px;
}

.countDown-number-2 {
	display: flex;
	justify-content: center;
	font-size: 15px;
	padding-top: 5px;
}

.seperator {
	margin: 0px 5px 10px 5px;
	font-size: 30px;
	color: #eb5c20;
}

.button {
	padding: 17px 0 17px 0;
	height: 50px;
	background: #e91e63;
	color: #fff;
	position: relative;
	text-align: center;
	cursor: pointer;
	border: 0;
	margin: 13px auto 12px auto;
	display: block;
	width: 100%;
	max-width: 240px;
}

.outline {
	background: 0 0;
	border: 2px solid #e91e63;
	padding: 15px 0 15px 0;
}

a.animated-button.thar-three {
	color: #fff;
	cursor: pointer;
	display: flex;
	position: relative;
	border: 3px solid #eb5c20;
	transition: all 0.4s cubic-bezier(0.42, 0, 0.58, 1);
	justify-content: center;
	align-items: center;
}
a.animated-button.thar-three:hover {
	color: #000 !important;
	background-color: transparent;
	text-shadow: nthree;
}
a.animated-button.thar-three:hover:before {
	left: 0%;
	right: auto;
	width: 100%;
}
a.animated-button.thar-three:before {
	display: block;
	position: absolute;
	top: 0px;
	right: 0px;
	height: 100%;
	width: 0px;
	z-index: -1;
	content: "";
	color: #000 !important;
	background: #eb5c20;
	transition: all 0.4s cubic-bezier(0.42, 0, 0.58, 1);
}

