.grapes .Caption {
  position: fixed;
  width: 100%;
  text-align: center;
  top: 3rem;
  font-size: 2rem;
  color: purple;
  opacity: 0.75;
}

.grapes .Grape {
  --layers: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  /* transform: translateY(calc(var(--layers) * 1rem + 10vh)) scale(0.5); */
  -webkit-transform: translateY(calc(-13.33px * var(--layers) + 26.66px)) scale(0.5);
  transform: translateY(calc(-13.33px * var(--layers) + 26.66px)) scale(0.5);
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.grapes .Grape .Leaves {
  position: relative;
  -webkit-transform-origin: 50% 100%;
  transform-origin: 50% 100%;
  -webkit-transform: scale(calc(var(--layers) * 0.2 + 0.8));
  transform: scale(calc(var(--layers) * 0.2 + 0.8));
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
  cursor: pointer;
}

.grapes .Grape .Leaves .Leaf {
  position: absolute;
  background-color: green;
  border-radius: 50%;
  border: solid 2px rgba(0, 0, 0, 0.3);
}

.Grape .Fruits .Row .Fruit {
  --digits: 1;
  --delay: 0;
  --variant: 0;
  --zindex: 0;
  width: 5rem;
  height: 5rem;
  -webkit-transform: scale(1.5);
  transform: scale(1.5);
  position: relative;
  background-color: hsl(calc(-90deg + var(--variant) * 60deg), 55%, 40%);
  border-radius: 100%;
  border: solid 2px rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-animation: pop 0.5s cubic-bezier(0.23, 1.25, 0.74, 1.33);
  animation: pop 0.5s cubic-bezier(0.23, 1.25, 0.74, 1.33);
  -webkit-animation-fill-mode: backwards;
  animation-fill-mode: backwards;
  -webkit-animation-delay: calc(var(--delay) * 0.1s);
  animation-delay: calc(var(--delay) * 0.1s);
  font-size: calc(2.5rem / (0.7 + var(--digits) * 0.3));
  color: #fff;
  z-index: var(--zindex);
  transition: z-index 0.5s, -webkit-transform 0.5s;
  transition: transform 0.5s, z-index 0.5s;
  transition: transform 0.5s, z-index 0.5s, -webkit-transform 0.5s;
  cursor: pointer;
}

.grapes .Grape .Leaves .Leaf:first-child {
  -webkit-transform-origin: 100% 100%;
  transform-origin: 100% 100%;
  -webkit-transform: translate(-110%, -50%) rotate(30deg);
  transform: translate(-110%, -50%) rotate(30deg);
  -webkit-animation: wiggleL 5s ease-in-out infinite;
  animation: wiggleL 5s ease-in-out infinite;
}

.grapes .Grape .Leaves .Leaf:last-child {
  -webkit-transform-origin: 0% 100%;
  transform-origin: 0% 100%;
  -webkit-transform: translate(10%, -50%) scale(1.2) rotate(-30deg);
  transform: translate(10%, -50%) scale(1.2) rotate(-30deg);
  -webkit-animation: wiggleR 5s ease-in-out infinite;
  animation: wiggleR 5s ease-in-out infinite;
}

.grapes .Grape .Leaves .Leaf::after {
  content: "";
  display: block;
  position: absolute;
  width: 80%;
  height: 2rem;
  border-radius: 50%;
  border-top: 2px solid darkgreen;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

