  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-animation: expand 0.5s cubic-bezier(0.78, 0.29, 0.51, 1.21);
  animation: expand 0.5s cubic-bezier(0.78, 0.29, 0.51, 1.21);
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

