.meteor {
	position: absolute;
	top: 0;
	right: 0;
	width: 5rem;
	opacity: 0.6;
}

.planet {
	top: -30vh;
	position: absolute;
	background-size: cover;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	font-weight: 600;
	text-shadow: 0px 0px 3px rgba(0, 0, 0, 0.4);
	display: none;
}
.planet-1 {
	width: 17rem;
	height: 17rem;
	right: 5vw;
	color: #b7ff45;
	font-size: 2.5rem;
	background-image: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/830072/planet-code.svg");
}
.planet-2 {
	left: 5vw;
	width: 17rem;
	height: 17rem;
	color: #48eafa;
	font-size: 2.8rem;
	background-image: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/830072/planet-web%402x.png");
}
.planet-3 {
	right: 5vw;
	width: 14rem;
	height: 14rem;
	color: #ffdc75;
	font-size: 2.6rem;
	background-image: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/830072/planet-3.svg");
