	left: 5vw;
	width: 17rem;
	height: 17rem;
	color: #a7bcff;
	font-size: 2.4rem;
	background-image: url("https://s3-us-west-2.amazonaws.com/s.cdpn.io/830072/planet-animation.svg");
}

.text {
	position: absolute;
	top: 0;
	bottom: 0;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	font-family: "Varela Round";
	color: white;
	font-size: 40px;
	text-shadow: 0 0 10px wheat;
	font-weight: bold;
	opacity: 0;
}

@media only screen and (max-width: 500px) {
	.text {
		font-size: 6vw;
	}
}

.SpaceBar {
	position: relative;
	height: 100vh;

	display: -webkit-box;
	display: -ms-flexbox;
	display: contents;
	-webkit-box-pack: center;
	-ms-flex-pack: center;
	justify-content: center;
	-webkit-box-align: end;
	-ms-flex-align: end;
	align-items: flex-end;
	overflow: hidden;
	font-size: 62.5%;
}

.Spacebody {
	overflow: hidden;
